<template>
    <div class="index_tiezi">
      <scroller style="top: 0;"
                :on-refresh="refresh"
                :on-infinite="infinite"
                ref="my_scroller"
                v-show="!showNoData"
      >
        <div  class="clearfix tiezi-cc-box" id="tiezi-scroll">
            <ul>
                <li class="tiezi-item" v-for="(item,index) in articles" v-show="item.status===1 || item.status==='1'">
                    <div class="line">
                    </div>
                    <div class="who-tiezi-item1 clearfix">
                        <img v-lazy="item.avatar" alt="" class="tiezi-pic">
                        <div class="who-publish-box">
                            <p class="who-publish-name">
                                {{item.author}}
                            </p>
                            <div class="who-publish-times">
                                <span class="who-publish-time">
                                   {{item.created_at | filterTime}}
                                </span>
                                <span class="who-publish-tag"  v-show="tags.length>0">
                                    来自标签：{{tagName}}
                                </span>
                            </div>
                        </div>
                        <a class="index-shouchang-tiezi" v-show="item.deleted !== 1 || item.deleted === '1'" :id="'shouc'+item.id" @click="shouC($event,item.id,item.collection_or_cancel)">
                            <span v-show="item.collection_or_cancel ===0 || item.collection_or_cancel ==='0'">＋收藏</span>
                            <span v-show="item.collection_or_cancel ===1 || item.collection_or_cancel ==='1'">已收藏</span>
                        </a>
                        <a class="delete-tiezi" v-show="item.deleted === 1 || item.deleted === '1'" @click="delArticle($event,item.id,index)" :id="'item'+item.id">
                          删除
                        </a>
                    </div>
                    <div class="who-tiezi-item2">
                        <router-link  class="who-tiezi-info" :to="'/tiezidetail/'+item.id">
                            <p class="index-tiezi-title">
                                {{item.title}}
                            </p>
                            <p class="index-tiezi-content" v-html="item.body">
                              {{item.body}}
                            </p>

                        </router-link>
                    </div>
                    <div class="who-tiezi-item3 clearfix">
                        <div class="tiezi-read-box">
                            <i class="read-icon"></i>
                            <span class="tiezi-read-num">
                                {{item.article_views_count}}
                            </span>
                        </div>
                        <div class="tiezi-zanfh-box clearfix">
                            <div class="tiezi-hui-box">
                                <span class="tiezi-hui-icon">
                                </span>
                                <span class="tiezi-hui-text">
                                    {{item.article_comments_count}}
                                </span>
                            </div>
                            <div class="tiezi-zan-box">
                                <span class="tiezi-zan-icon">
                                </span>
                                <span class="tiezi-zan-text">
                                   {{item.article_likes_count}}
                                </span>
                            </div>
                        </div>
                    </div>
                </li>

            </ul>
        </div>
      </scroller>
      <!--<back-top :myTieZiPos="myTieZiPos"  v-show="!showNoData" @click="goTop"></back-top>-->
      <NoData v-show="showNoData"></NoData>
      <div class="send-tie-box">
          <div class="tie-send-plus">
            <img src="../../assets/images/send_plus.png" alt="" class="plus-icon">
          </div>
          <div class="tie-send-text" @click="goPublicTiezi()">
            发布
          </div>
      </div>
    </div>
</template>

<script>
    import Api from '../../api/api';
//    import backTop from '../backtop/backtop.vue';
    import NoData from '../nodata/NoData.vue';
    import logUtil from '../../utils/log'
    import {getTieZiInfo} from '../../api/tiezi'
    import {collect} from '../../api/tiezi'
    import {unCollect} from '../../api/tiezi'
    import {deleteArticle} from '../../api/tiezi'
    export default{
       props:{
         'tieziFlag':Number
       },
       components:{
//         backTop,
         NoData
       },
       data(){
         return{
           showNoData:false,
//           myTieZiPos:-1,
           collection_or_cancel:-1,
           //总页数
           totalCount:0,
           //第一页
           pageIndex:1,
           //每一页的数量
           pageSize:10,
           articles:[],
           //类型：0表示初始化数据，1表示下拉刷新，2表示加载更多
           type:0,
           //表示收藏
           isShou:true,
           tags:[],
           tagName:""
         }
       },
       mounted(){
         //stop loading more
         this.$refs.my_scroller.finishInfinite(true);
         this.getArticleInfos(0,1);
       },
      filters:{
        filterTime(value){
          return value.split("T")[0];
        }
      },
       methods:{
         goPublicTiezi(){
           this.$router.push({path: '/writetie', replace: true});
         },
         /*goTop(){
           this.$refs.my_scroller.scrollTo(0,0,true);
         },*/
         delArticle($event,id,index){
             let token = localStorage.getItem('token');
             const _this = this;
             if(token){
               deleteArticle(Api.NEWSCOMMENTINFOS,id,index).then((res)=>{
                 if(res.status === 204){
                   _this.showMsg('恭喜您，删除成功!');
                   _this.$router.go(0);
                 }
               },(err)=>{
                  _this.showMsg('删除失败，请重试!');
                  _this.$store.dispatch('hideLoading');
                })
             }else{
               this.$router.push({path: '/login', replace: true});
             }
             $event.preventDefault();
         },
         dealCollection(id,tar){
           const _this = this;
           collect(Api.COLLECTIONS,id,tar).then((res)=>{
             if(res.status === 200 && res.data.data.detail!==null){
               $(tar).html('已收藏');
               _this.showMsg('收藏成功!');
               this.collection_or_cancel = 1;
             }
           },(err)=>{
             _this.$store.dispatch('hideLoading');
           });
         },
         cancleCollection(id,tar){
           const _this = this;
           unCollect(Api.COLLECTIONS,id,tar).then((res)=>{
             if(res.status === 204){
               $(tar).html('+收藏');
               //已经收藏了
               this.collection_or_cancel = 0;
               _this.showMsg('已取消收藏!');
             }
           },(err)=>{
             _this.showMsg('取消收藏失败，请重试!');
             _this.$store.dispatch('hideLoading');
           });
         },
           /**
            * 收藏帖子
            * */
           shouC($event,id,collection_or_cancel){
             this.collection_or_cancel = collection_or_cancel;
             let token = localStorage.getItem('token');
             let tar = event.currentTarget;
             const _this = this;
             if(token){
               if(id!==null || id!=='' || id!=='undefine'){
                 switch (this.collection_or_cancel){
                   case 0://未收藏
                        _this.dealCollection(id,tar);
                       break;
                   case 1:
                       _this.cancleCollection(id,tar);
                       break;
                 }
               }
             }else{
               //需要先登录
               this.$router.push({path: '/login', replace: true});
             }
             $event.preventDefault();
         },
           showMsg(str){
           this.$toast({
             message: str,
             duration: 2000,
           });
         },
           /**
            * 从服务器端获取文章(帖子)的数据
            * */
           getArticleInfos(cType,page){
               if(cType === 2){
                   this.$store.dispatch('hideLoading');
               }
               const _this = this;
                getTieZiInfo(Api.ARTICLES,page,_this.pageSize).then((res)=>{
                  if(res.status === 200 && res.data.data!==null){
                    _this.initDatas(cType,res);
                  }else{
                    _this.dealErr();
                  }
                },(err)=>{
                  _this.dealErr();
                });
           },
         dealErr(){
           this.showNoData = true;
           this.$store.dispatch('hideLoading');
         },
         /**
          * 处理获取到的数据并渲染到view层上
          * */
         initDatas(cType,res) {
           let tempArr = [];
           this.totalCount = res.data.data.total;
           tempArr = res.data.data.detail;

           logUtil.printLog("获取到的帖子数据是:",res);
           if(tempArr.length>0){
             this.showNoData = false;
             if(cType===0){
               this.articles = tempArr;
             }
             //下拉刷新
             else if(cType===1){
               this.articles=tempArr.splice(0,this.pageSize);
             }else if(cType === 2){ //上拉加载
               this.articles = this.articles.concat(tempArr);
             }
             const len1 = this.articles.length;
             for(let i = 0;i<len1;i++){
               this.tags = this.articles[i].tags;
             }
             this.tagName = "";
             let len = this.tags.length;
             for(let i=0;i<len;i++){
               if(i<=2){
                 if(i+1===len){
                   if(i>=2){
                     this.tagName+=this.tags[i].name+"......";
                   }else{
                     this.tagName+=this.tags[i].name;
                   }

                 }else {
                   this.tagName+=this.tags[i].name+"、";
                 }
               }
             }
             const articleLen = this.articles.length;
             for(let i=0;i<articleLen;i++){
                 let indexArticle = this.articles[i].body.indexOf("<img");
                 if(indexArticle!==-1){
                   this.articles[i].body = this.articles[i].body.replace(/(<img.*?>)/g, '');
                 }
             }

           }else{
             this.showNoData = true;
           }
         },
         /**
          * 刷新
          * */
         refresh(done) {
           const that = this;
           that.pageIndex = 1;
           that.type = 1;
           setTimeout(() => {
             that.getArticleInfos(that.type,that.pageIndex);
             done()
           }, 1500)
         },
         /**
          * 加载更多
          * */
         infinite(done) {
           const that = this;
           that.pageIndex++;
           that.type = 2;
           setTimeout(() => {
             //当前页数与每页记录数的乘机大于总记录数就不在加载更多
             if(that.pageIndex>=that.totalCount){
               done(true);
               return;
             }
             done();
             that.getArticleInfos(that.type,that.pageIndex);
//             that.myTieZiPos = that.$refs.my_scroller.getPosition().top;
           }, 1500)
         },
       },

    }
</script>
<style scoped>
    @import "../../assets/css/tiezi.css";
    body {
        background-color: #EDEEF0;
    }

</style>



